import React, { useState, useEffect } from 'react'

const Search = (props) => {
  const { listToBeSearched, text, setItems, clear, itemsSelected } = props
  const [searchResults, setSearchResults] = useState([])

  useEffect(() => {
    const updateSearchResults = () => {
      const toBeAppended = []
      if (text === '') {
        setSearchResults(toBeAppended)
        return
      }
      for (let i = 0; i < listToBeSearched.length; i++) {
        const txtVal = listToBeSearched[i].authorName || listToBeSearched[i].topicName 
        if (txtVal.toUpperCase().indexOf(text.toUpperCase()) > -1)
          toBeAppended.push(listToBeSearched[i])
      }
      setSearchResults(toBeAppended)
    }
    updateSearchResults()
  }, [text, listToBeSearched])

  return searchResults.length > 0 ? (
    <div className='search flex flex-column'>
      {searchResults.map((result, key) => {
        return (
          <div
            className='search-result'
            onClick={() => {
              if(itemsSelected.indexOf(result) === -1)
                setItems([...itemsSelected, result])

              clear('')
            }}
            key={key}
          >
            {result.authorName || result.topicName}
          </div>
        )
      })}
    </div>
  ) : (
    ''
  )
}

export default Search
